.Title {
  position: absolute;
  top: 0;
  width: 100%;
  text-align: center;
  color: white;
  background: -webkit-gradient(linear, left top, left bottom, from(#00BFFF), to(#00A6DE));
  background: -webkit-linear-gradient(#00BFFF, #00A6DE);
  background: -o-linear-gradient(#00BFFF, #00A6DE);
  background: linear-gradient(#00BFFF, #00A6DE);
  padding: 12px;
  -webkit-box-shadow: 1px 0 10px #058FBE;
          box-shadow: 1px 0 10px #058FBE;
  border-bottom: 1px solid #058FBE;
}


