* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*:focus {
  outline: none;
}

.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

html {
  font-size: 18px;  
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#EFF4FF), color-stop(#E8EAFF), to(#EFF4FF));
  background: -webkit-linear-gradient(left, #EFF4FF, #E8EAFF, #EFF4FF);
  background: -o-linear-gradient(left, #EFF4FF, #E8EAFF, #EFF4FF);
  background: linear-gradient(to right, #EFF4FF, #E8EAFF, #EFF4FF);
}

ul {
  list-style: none;
}

@media only screen and (min-width: 768px) {
  html {
    font-size: 24px;
  }
}

@media only screen and (min-width: 1024px) {
  html {
    font-size: 30px;
  }
}

@media only screen and (min-width: 1200px) {
  html {
    font-size: 18px;
  }
}


