.Buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.btn {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.6rem;
  font-weight: 600;
  padding: 12px 16px;
  border: 1px solid transparent;
  border-radius: 6px;
  color: #fff;
  cursor: pointer;
  width: 49%;
}

.edit-btn {
  background: -webkit-gradient(linear, left top, right top, from(#FFD700), to(#FFC04D));
  background: -webkit-linear-gradient(left, #FFD700, #FFC04D);
  background: -o-linear-gradient(left, #FFD700, #FFC04D);
  background: linear-gradient(to right, #FFD700, #FFC04D);
  border: 1px solid #FFBE4A;
}

.edit-btn:hover {
  background: #FCB83F;
}

.del-btn {
  background: -webkit-gradient(linear, left top, right top, from(#F63434), to(#AB0303));
  background: -webkit-linear-gradient(left, #F63434, #AB0303);
  background: -o-linear-gradient(left, #F63434, #AB0303);
  background: linear-gradient(to right, #F63434, #AB0303);
  border: 1px solid #F94242;
}

.del-btn:hover {
  background: #F94242;
}

@media only screen and (min-width: 1200px) {
  .Buttons {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
  
  .btn {
    width: 15%;
  }

  .edit-btn {
    margin-right: 10px;
  }
}


