.Recipes {
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(#F3FFE2), to(#DDFFDE));
  background: -webkit-linear-gradient(#F3FFE2, #DDFFDE);
  background: -o-linear-gradient(#F3FFE2, #DDFFDE);
  background: linear-gradient(#F3FFE2, #DDFFDE);
  border: 1px solid #4f4f4f;
  border-radius: 6px;
  padding: 12px;
}


